.swap {
    width: 100%;
    height: 400px;
    padding: 0;
    box-sizing: border-box;
    display: grid;
    align-items: center;

    .from-bg {
        width: 50%;
        height: 100px;
        margin-left: 25.5%;
        background: #FAFAFA;
        border-radius: 48.5px;

        .from {
            margin-left: 7%;
            margin-top: 5%;
        }

        .frominput {
            margin-left: 6%;
            width: 50%;
            font-family: Helvetica;
            font-size: 24px;
            outline: none;
            border: none;
            background: #FAFAFA;
        }

        .fromselect {
            margin-left: 60%;
            margin-top: -8%;
        }
    }

    .arrow {
        margin: 0 auto;
    }

    .to-bg {
        width: 50%;
        height: 100px;
        margin-left: 25.5%;
        background: #FAFAFA;
        border-radius: 48.5px;

        .to {
            margin-left: 7%;
            margin-top: 5%;
        }

        .toinput {
            margin-left: 6%;
            width: 50%;
            font-family: Helvetica;
            font-size: 24px;
            outline: none;
            border: none;
            background: #FAFAFA;
        }

        .toselect {
            margin-left: 60%;
            margin-top: -8%;
        }
    }

    .feetag {
        text-align: center;
        font-family: Helvetica;
        font-size: 14px;
    }

    .btcaddress {
        width: 55%;
        text-align: center;
        margin: 0 23%;
        border: none;
        outline: none;
        font-family: Helvetica;
        font-size: 24px;
        border-bottom: 1px solid;
    }

    .swapbutton {
        width: 25%;
        height: 50px;
        margin: 0 auto;
        background: #F7931A;
        border-radius: 25px;
        border-style: none;
        font-family: Helvetica;
        font-size: 16px;
        color: #FFFFFF;
        cursor: pointer;
        outline: none;
    }
}

@media screen and (max-width: 1024px) {
    .swap {
        .from-bg {
            width: 55%;
            height: 90px;
            margin-left: 23%;
            .from {
                margin-left: 7%;
                margin-top: 5%;
            }
            .frominput {
                width: 40%;
                margin-top: 2%;
            }
            .fromselect {
                margin-left: 58%;
                margin-top: -10%;
            }
        }

        .to-bg {
            width: 55%;
            height: 90px;
            margin-left: 23%;
            .to {
                margin-left: 7%;
                margin-top: 5%;
            }
            .toinput {
                width: 40%;
                margin-top: 2%;
            }
            .toselect {
                margin-left: 58%;
                margin-top: -10%;
            }
        }

        .swapbutton {
            width: 30%;
            height: 6vh;
        }
    }
}

@media screen and (max-width: 768px) {
    .swap {
        .from-bg {
            width: 70%;
            height: 80px;
            margin-left: 15%;
            .frominput {
                font-size: 21px;
            }
            .fromselect {
                margin-left: 55%;
            }
        }

        .to-bg {
            width: 70%;
            height: 80px;
            margin-left: 15%;
            .toinput {
                font-size: 21px;
            }
            .toselect {
                margin-left: 55%;
            }
        }

        .btcaddress {
            font-size: 20px;
        }

        .swapbutton {
            width: 35%;
            height: 6vh;
        }
    }
}

@media screen and (max-width: 626px) {
    .swap {
        .from-bg {
            width: 80%;
            height: 85px;
            margin-left: 11%;
            .fromselect {
                margin-left: 52%;
                margin-top: -11%;
            }
        }

        .to-bg {
            width: 80%;
            height: 85px;
            margin-left: 11%;
            .toselect {
                margin-left: 52%;
                margin-top: -11%;
            }
        }

        .swapbutton {
            width: 40%;
            height: 6vh;
        }
    }
}

@media screen and (max-width: 479px) {
    .swap {
        .from-bg {
            width: 100%;
            height: 85px;
            margin-left: 4%;

            .frominput {
                width: 30%;
                margin-top: 3%;
                font-size: 20px;
            }

            .fromselect {
                margin-left: 47%;
                margin-top: -12%;
            }
        }

        .to-bg {
            width: 100%;
            height: 85px;
            margin-left: 4%;

            .toinput {
                width: 30%;
                margin-top: 3%;
                font-size: 20px;
            }

            .toselect {
                margin-left: 47%;
                margin-top: -12%;
            }
        }

        .btcaddress {
            font-size: 22px;
        }

        .swapbutton {
            width: 55%;
            height: 6vh;
        }
    }
}

@media screen and (max-width: 375px) {
    .swap {
        .from-bg {
            width: 110%;
            height: 80px;
            margin-left: 0;
            .frominput {
                font-size: 18px;
            }
            .fromselect {
                margin-left: 42%;
                margin-top: -15%;
            }
        }

        .to-bg {
            width: 110%;
            height: 80px;
            margin-left: 0;
            .toinput {
                font-size: 18px;
            }
            .toselect {
                margin-left: 42%;
                margin-top: -15%;
            }
        }

        .btcaddress {
            font-size: 18px;
        }

        .swapbutton {
            width: 55%;
        }
    }
}

@media screen and (max-width: 320px) {
    .swap {
        .from-bg {
            .frominput {
                font-size: 18px;
            }
            .fromselect {
                margin-left: 36%;
                margin-top: -17%;
            }
        }

        .to-bg {
            .toinput {
                font-size: 18px;
            }
            .toselect {
                margin-left: 36%;
                margin-top: -17%;
            }
        }

        .btcaddress {
            font-size: 16px;
        }
    }
}

