.header {
    width: 80%;
    margin: 0;
    padding: 5%;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 10%;

    img {
        margin: 0;
        width: 20%;
        height: 10%;
    }

    button {
        width: 12%;
        height: 6vh;
        font-size: 14px;
        border-radius: 21.5px;
        border: 1px solid #F7931A;
        background-color: #FAFAFA;
        font-family: Helvetica;
        color: #F7931A;
        text-align: center;
        cursor: pointer;
        outline: none;
    }
}

@media screen and (max-width: 1024px) {
    .header {
        img {
            width: 25%;
        }
        button {
            width: 17%;
            height: 4vh;
        }
    }
}

@media screen and (max-width: 768px) {
    .header {
        img {
            width: 30%;
        }
        button {
            width: 22%;
            height: 4vh;
            font-size: 12px;
        }
    }
}

@media screen and (max-width: 479px) {
    .header {
        justify-content: center;
        img {
            width: 50%;
        }
        button {
            display: none;
        }
    }
}