.footer{
    height: 18px;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 70%;
    margin: 20px 15%;
    &-logo {
        width: 10%;
        display: flex;
        align-items:center;
        justify-content:space-around;
        margin:0 10px;
    }
}
 
@media screen and (max-width: 479px){
    .footer {
        margin-top:40px;
        &-logo {
            width:20%;
        }
    }
}

